<template>
    <div>
        <div id="content">
            <div class="content_m">
                <form id="dologin" >
                
                    <div class="login_m">
                        <div class="l_box">
                            <div class="l_box_l">手机/邮箱</div><div class="l_box_r"><input v-model="username" type="text" id="username" name="username" texttype="text" value="" backText1="请输入注册时的邮箱或手机号码。" regXe="/(^([a-zA-Z0-9._-])+@([a-zA-Z0-9._-])+(.[a-zA-Z]{2,3})$)|(^0?(13[0-9]|15[012356789]|18[01236789]|14[57])[0-9]{8}$)/" backText2="请输入正确的邮箱地址或11位手机号码。"  /></div><div class="clear"></div>
                        </div>
                        <div class="l_box">
                            <div class="l_box_l">密&nbsp;　　码</div><div class="l_box_r"><input v-model="password" type="password" id="password" name="password" texttype="text" backText1="请输入您的密码。" /></div><div class="clear"></div>
                        </div>
                    </div>
                    
                    <button class="login_btn_n" type="button" @click="denglu">立即登录</button>
                    <p class="b_log">您还没有美西账号？ <router-link tag="a" :to="{name:'reg'}">立即注册</router-link></p>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name:"login",
    data(){
        return {
            username:"",
            password:"",
            mess:''
        }
    },
    methods: {
        denglu(){
            this.$store.dispatch(
                'UPDATE_USER',
                {
                username:this.username,
                password:this.password
            }).then(
                data=>{
                    if(data.error===0){
                        this.mess = data.mess;
                        this.$router.push('/user')
                    }else{
                        this.mess = data.mess
                    }
                }
            )
        } 
    }
}
</script>

<style scoped>
#content {padding-top:45px;}
.nav_bd {border:1px solid #ccc; border-radius:5px;margin-top:10px;}
.content_m {max-width:640px; padding:2% 2% 0 2%; margin:0 auto;}
.login_m {border:1px solid #ccc; border-radius:5px; box-shadow:0 0 5px #ccc;}
.l_box, .pasa {padding:15px 0; border-bottom:1px solid #ccc; height:20px;}
.l_box .l_box_l, .l_box .l_box_r {float:left;}
.l_box input {width:90%; height:20px; font-size:14px; border:none; color:#333;}
.l_box_l {width:30%; height:26px; text-align:center;}
.l_box_r {width:70%; height:26px;}
.heiAuto {height:auto;}
.hei70 {height:70px;}
.hei75 {height:75px;}
.hei50 {height:50px;}
.login_btn_n {width:100%; height:40px; margin:15px 0 10px 0; background:#8e0c3a; border-radius:3px; color:#fff; text-shadow:1px 1px 2px #333; font-size:14px;}
.b_log {padding:5px; text-align:right; height:20px;}
.b_log a {color:#8e0c3a;}
</style>
